import { Component, Element, h, Host, Prop } from '@stencil/core'

@Component({
  tag: 'ato-col',
  shadow: false,
})
export class AtoCol {
  @Prop() size?: 'auto' | string
  @Prop() sizeSm?: string
  @Prop() sizeMd?: string
  @Prop() sizeLg?: string
  @Prop() sizeXl?: string
  @Prop() offset?: string
  @Prop() offsetSm?: string
  @Prop() offsetMd?: string
  @Prop() offsetLg?: string
  @Prop() offsetXl?: string
  @Prop() push?: string
  @Prop() pull?: string

  @Element() private element: HTMLElement

  componentDidLoad() {
    const parent = this.element.parentElement
    if (parent) {
      this.element.outerHTML = this.element.outerHTML.replace(/ato/g, 'ion')
    }
  }

  render() {
    return (
      <Host
        size={this.size}
        sizeSm={this.sizeSm}
        sizeMd={this.sizeMd}
        sizeLg={this.sizeLg}
        sizeXl={this.sizeXl}
        offset={this.offset}
        offsetSm={this.offsetSm}
        offsetMd={this.offsetMd}
        offsetLg={this.offsetLg}
        offsetXl={this.offset}
        push={this.push}
        pull={this.pull}
      >
        <slot />
      </Host>
    )
  }
}
